/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

import { Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl, PanelColorSettings } from '@wordpress/block-editor';
import { RangeControl, PanelBody, ToggleControl } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

export default function Edit( props ) {

	const { setAttributes, attributes } = props;
	const { showTitle,
		titleColor,
		titleBackgroundColor,
		showIcon,
		iconColor,
		iconBackgroundColor,
		showTimespan,
		timespanColor,
		timespanBackgroundColor,
	} = attributes;

	return (
		<div { ...useBlockProps() }>
			<InspectorControls>
				<PanelBody title={__('Title', 'wp-travel-blocks')}>
					<ToggleControl
						label={ __( 'Show Title', 'wp-travel-blocks' ) }
						checked={ showTitle }
						onChange={ () => setAttributes( { showTitle: ! showTitle } ) }
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: titleColor,
								onChange: colorValue => setAttributes({ titleColor: colorValue }),
								label: __( "Text", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: titleBackgroundColor,
								onChange: colorValue => setAttributes({ titleBackgroundColor: colorValue }),
								label: __( "Background", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
				</PanelBody>
				<PanelBody title={__('Icon', 'wp-travel-blocks')} initialOpen={ false }>
					<ToggleControl
						label={ __( 'Show Icon', 'wp-travel-blocks' ) }
						checked={ showIcon }
						onChange={ () => setAttributes( { showIcon: ! showIcon } ) }
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: iconColor,
								onChange: colorValue => setAttributes({ iconColor: colorValue }),
								label: __( "Icon", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: iconBackgroundColor,
								onChange: colorValue => setAttributes({ iconBackgroundColor: colorValue }),
								label: __( "Background", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
				</PanelBody>
				<PanelBody title={__('Timespan', 'wp-travel-blocks')} initialOpen={ false }>
					<ToggleControl
						label={ __( 'Show Timespan', 'wp-travel-blocks' ) }
						checked={ showTimespan }
						onChange={ () => setAttributes( { showTimespan: ! showTimespan } ) }
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: timespanColor,
								onChange: colorValue => setAttributes({ timespanColor: colorValue }),
								label: __( "Text", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
					<PanelColorSettings
						className={`timespan-custom-color-palette-style`}
						colorSettings={[
							{
								value: timespanBackgroundColor,
								onChange: colorValue => setAttributes({ timespanBackgroundColor: colorValue }),
								label: __( "Background", 'wp-travel-blocks' )
							},
						]}
						initialOpen={false}
					/>
				</PanelBody>
			</InspectorControls>
			<ServerSideRender
				block="wptravel/trip-timespan"
				attributes={ props.attributes }
			/>
		</div>
	);
}
